
.sidebar {
  background: rgb(61, 79, 93);
  color: #fff;
}
.header {
  text-align: center;
  top: auto;
  margin: 1em auto;
}
.logo {
  border: 5px solid #fff;
  border-radius: 50%;
  width: 106px;
  height: 106px;
  margin: 0 auto;
  background: rgb(176, 202, 219);
  display: block;
  text-align: center;
}
img.lazy-load {
  width: 20%;
  height: 20%;
  border-radius: 50%;
  opacity: 0;
  transition: all 0.2s ease-in;
}
img.lazy-load.show {
  width: 100%;
  height: 100%;
  opacity: 1;
}
.brand-title,
.brand-tagline {
  margin: 0;
}
.brand-title {
  text-transform: uppercase;
  margin: 0.67em 0;
}
.brand-tagline {
  font-weight: 300;
  color: rgb(176, 202, 219);
}
.nav-list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.nav-item {
  display: inline-block;
  *display: inline;
  zoom: 1;
}
.nav-item a {
  background: transparent;
  border: 2px solid rgb(176, 202, 219);
  color: #fff;
  margin-top: 1em;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  font-size: 85%;
}
.nav-item a:hover,
.nav-item a:focus {
  border: 2px solid rgb(61, 146, 201);
  text-decoration: none;
}
.nav-item a.active {
  border: 2px solid rgb(61, 146, 201);
  text-decoration: none;
}
.footer ul {
  padding: 0;
}
.footer a {
  /* color: #ccc;
  font-size: 80%; */
}
.footer .pure-menu a:hover,
.footer .pure-menu a:focus {
  background: none;
}
.sidebar .footer {
  padding: 0;
  font-size: 1.2em;
  margin: 0;
}
.sidebar .footer .pure-menu-link {
  padding: 0 .5em;
}
ul.category-popup li {
  padding: 0;
  margin: 0;
  height: 100%;
  display: inline-block;
  zoom: 1;
  vertical-align: middle;
  position: relative;
}
.post-category {
  margin: 0 0.1em;
  padding: 0.3em 1em;
  color: #fff;
  background: #999;
  font-size: 80%;
}
.post-category-Design {
  background: #990066;
}
.post-category-Wiki {
  background: #5aba59;
}
.post-category-Icyse {
  background: #4d85d1;
}
.post-category-Algorithm {
  background: #FF0033;
}
.post-category-MongoDB {
  background: #8156a7;
}
.post-category-JavaScript {
  background: maroon;
}
.post-category-GameJs {
  background: #df2d4f;
}
.post-category-NodeJs {
  background: #33CC33;
}
.post-category-Linux {
  background: #df6659;
}
.post-category-AngularJs {
  background: peru;
}
.post-category-React {
  background: #339999;
}
.post-category-HTML {
  background: #FF9900;
}
.post-category-CSS {
  background: #ffc94c;
}
.post-category-Canvas {
  background: #64448f;
}
.post-category-Git {
  background: #3498DB;
}
.post-category-Interview {
  background: #3755ad;
}




