
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /*text-align: center;*/
  color: #2c3e50;
  /*margin-top: 60px;*/
}
#fork-me {
  position: fixed;
  top: 0;
  left: 0;
  border: 0;
  z-index: 1;
  width: 25%;
  max-width: 149px;
  min-width: 96px;
}
#fork-me img {
  border: 0;
  width: 100%;
}
#layout {
  padding: 0;
}
.content {
  padding: 2em 1em 0;
}
.content-subhead {
  text-transform: uppercase;
  color: #aaa;
  border-bottom: 1px solid silver;
  padding: 0.4em 0;
  font-size: 80%;
  font-weight: 500;
  letter-spacing: 0.1em;
}
.post {
  padding-bottom: 2em;
}
.post-title {
  font-size: 1.5em;
  margin-bottom: 0.2em;
}
.post-title a {
  color: #222;
}
.post-avatar {
  border-radius: 50px;
  float: right;
  margin-left: 1em;
}
.post-description {
  font-family: Georgia, "Cambria", serif;
  color: #444;
  line-height: 1.8em;
}
.post-meta {
  color: #999;
  font-size: 90%;
  margin: 0;
}
.footer {
  text-align: center;
  padding: 1em 0;
}
.child-view {
  position: absolute;
  transition: all .5s cubic-bezier(.55, 0, .1, 1);
}




